@import (less) "color.less";

.header-overlay {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; //background: rgba(255, 255, 255, 0.7);
  background: @lightest_gray;
}

.prev-page-btn-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  .container {
    display: table;
  }
  .prev-page-btn {
    display: table-cell;
    height: 3.125em;
    vertical-align: middle;
    color: @green;
    .prev-page-btn-arrow {
      font-size: 1.250em;
      vertical-align: middle;
    }
    .prev-page-btn-text {
      font-size: 0.875em;
    }
  }
}

.page-title-container {
  height: 3.125em;
  display: table-cell;
  vertical-align: middle;
  border-bottom: 1px solid @light_gray;
  .titles {
    display: table;
    table-layout: fixed;
    width: 100%;
  }
}

.page-overtitle {
  position: relative;
  font-size: 0.750em;
  text-align: center;
  margin-bottom: -0.417em;
  text-transform: uppercase;
}

.page-title {
  position: relative;
  font-size: 1.125em;
  text-align: center;
  font-weight: bold;
  margin: 0;
  text-transform: uppercase;
}

.page-subtitle {
  position: relative;
  font-size: 0.750em;
  text-align: center;
  margin-top: -0.417em;
}

.header-overlay.sticky {
    position: fixed;
    top:0;
    left: 0 !important;
    z-index: 9999 !important;
    height: inherit !important;
    width: inherit !important;
} 